import React, { Component } from 'react';
import { Datas } from '../components/Datas.js';
import '../App.css';
import AOS from 'aos';
import  'aos/dist/aos.css';

let data= Datas.map((index)=>{ return index;});
var wordIndex = 0;
let tempWord ;
class VocabCards extends Component {

    constructor(props) {
        super(props)
      
        this.state = {
          
             word:"",
        }
    
    
      }
      componentDidMount()
   {
      AOS.init();
   }

      handleWord = ( event ) => {
        this.setState({
          word: event.target.value
        })
          }
      searchWord = ( event ) => {
          tempWord = this.state.word;
          for(let i=0;i<data.length;i++)
              {
                let wordA = data[i].word.toLowerCase();
                let wordB = tempWord.toLowerCase();
                console.log(`${wordA},${wordB}`);
                console.log(wordA === wordB);
                if(wordA.localeCompare(wordB) === 0 ){
                
                  wordIndex = i;
                  document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
                  document.getElementById("containmean").innerHTML = `<p> </p>`;
                  document.getElementById("showHide").innerHTML = "MEANING";
                  document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
                  break;
                
                }
                else if(wordB ==="" ){
                  document.getElementById("containword").innerHTML = `<h2>Enter the word</h2>`;
                  document.getElementById("containmean").innerHTML = `<p> </p>`;
                  document.getElementById("showHide").innerHTML = "MEANING";
                  document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
                  break;
                }

                else{
                  document.getElementById("containword").innerHTML = `<h2>Word not found!</h2>`;
                  document.getElementById("containmean").innerHTML = `<p> </p>`;
                  document.getElementById("showHide").innerHTML = "MEANING";
                  document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
                  
                  
                }
              }
    event.preventDefault();
          
          }

    render() {

    

    function writeThingForward() {
    
     ;
    if ( wordIndex === data.length-1 ) 
    {
      wordIndex = 0;
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span> ${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
      document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    }

    else
    { wordIndex = wordIndex + 1;
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span> ${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
      document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    }
     
  }

  function writeThingForwardTen() {
    
     
    if ( wordIndex >= data.length-1 ) 
    {
      wordIndex = 0;
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
      document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    }

    else
    { wordIndex= wordIndex + 5;
      if ( wordIndex >= data.length-1 ) wordIndex = 0;
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
      document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    }
     
  }

  function writeThingBack() {
    
     
    if ( wordIndex === 0 ) 
    {
      wordIndex = data.length-1;
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
        document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    
    }
    
    else{
      wordIndex = wordIndex -1;
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
      document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
        
    }
     
  }

  function writeThingBackTen() {
    
     
    if ( wordIndex <= 0 ) 
    {
      wordIndex = data.length-1;
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
        document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    
    }
    
    else{
      wordIndex= wordIndex -5;
      if ( wordIndex <= 0 ) wordIndex = data.length-1;
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
      document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
        
    }
     
  }

  function writeThingMean() {

    var x =document.getElementById("containword");
    var b = x.innerHTML===`<h2>Enter the word</h2>`||x.innerHTML===`<h2>Word not found!</h2>`;
    console.log(document.getElementById("showHide").innerHTML === "MEANING");
    if(document.getElementById("showHide").innerHTML === "MEANING" && !b)
    {
      document.getElementById("showHide").innerHTML = "HIDE MEANING";
      document.getElementById("showHide").className += " active";
      document.getElementById("containmean").innerHTML = `
                                                            <h5>MEANING:</h5>
                                                            <p>${data[wordIndex].meaning}</p>
                                                            <p><strong>SYNONYMS</strong>: ${data[wordIndex].synonyms}</p>
                                                            <p><strong>ANTONYMS</strong>: ${data[wordIndex].antonyms}</p>
                                                          `; 
      document.getElementById("picContainer").innerHTML = `<img src=${data[wordIndex].images} alt="${data[wordIndex].word}" style="width:80%;max-height:400px"/>`
    }
    else if(document.getElementById("showHide").innerHTML !== "MEANING"&& b)
    {
      document.getElementById("showHide").innerHTML = "HIDE MEANING";
      document.getElementById("showHide").className += " active";
      document.getElementById("containmean").innerHTML ="";
      document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    
    }
    else{
      document.getElementById("showHide").innerHTML = "MEANING";
      document.getElementById("showHide").classList.remove("active")
      document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";

    }

    
  }

  function sortObjAsc(){
    data.sort(function(a, b){
      var nameA=a.word.toLowerCase(), nameB=b.word.toLowerCase()
      if (nameA < nameB) //sort string ascending
          return -1 
      if (nameA > nameB)
          return 1
      return 0 //default return value (no sorting)
  })

  document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
  document.getElementById("containmean").innerHTML = `<p> </p>`;
  document.getElementById("showHide").innerHTML = "MEANING";
  document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    
  }

  function sortObjDesc(){
    data.sort(function(a, b){
      var nameA=a.word.toLowerCase(), nameB=b.word.toLowerCase()
      if (nameA < nameB) //sort string ascending
          return -1 
      if (nameA > nameB)
          return 1
      return 0 //default return value (no sorting)
  });
  data.reverse();

  document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
  document.getElementById("containmean").innerHTML = `<p> </p>`;
  document.getElementById("showHide").innerHTML = "MEANING";
  document.getElementById("containmean").innerHTML ="";document.getElementById("picContainer").innerHTML =""; document.getElementById("similarWords").innerHTML="";
    
  }


  function writeThingRandom(){

    wordIndex = Math.floor(Math.random()*data.length);
    if(wordIndex !== data.length)
    { 
      document.getElementById("containword").innerHTML = `<h2><span style="color:#282c34;font-size:20px;">${data.indexOf(data[wordIndex]) + 1}.</span>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "MEANING";
      document.getElementById("containmean").innerHTML ="";
      document.getElementById("picContainer").innerHTML =""; 
      document.getElementById("similarWords").innerHTML="";
  }
  }

  function showSimilar()
  {
    var similarwords = document.getElementById("similarWords");
    var wordA = data[wordIndex].synonyms.split(",");
    var wordC = [""];
    var count;
    similarwords.innerHTML = "";
    if(wordA!="")
     {
       for(let i=0;i<data.length;i++)
       { var count = 0;
         var wordB = data[i].synonyms.split(",");
         if(wordB === "")continue;
         if(data[i].word===data[wordIndex].word)continue;
         for(let j=0;j<wordA.length;j++)
          {
            for(let k=0;k<wordB.length;k++)
              {
                if(wordA[j]===wordB[k])
                 { 
                   count++;
                   console.log(count);
                   if(count>0 )
                    { 
                      wordC.push(data[i].word)
                      
                    }
                  }
              }
            }
        }
        for(let i=0;i<wordC.length;i++)
        {
          similarwords.innerHTML+=`<li>${wordC[i]}</li>`;
        }
      }
    }
     
      function showMeaning(n){
      console.log(n);
      // document.getElementById(`mean${n}`).innerHTML=`
      //       <h5>MEANING:</h5>
      //       <p>${data[i].meaning}</p>
      //       <p><strong>SYNONYMS</strong>: ${data[i].synonyms}</p>
      //       <p><strong>ANTONYMS</strong>: ${data[i].antonyms}</p>
      // `
    }
    
  function displayAll(){
    var showall = document.getElementById("showAll");
    if(document.getElementById("button2").innerHTML==="HIDE ALL")
    {
      showall.innerHTML="";
      document.getElementById("button2").innerHTML="SHOW ALL WORDS"
    }
    else{
      document.getElementById("button2").innerHTML= "HIDE ALL"; 
    data.map(event=>{
       showall.innerHTML+=`
       <div class="row1" data-aos="fade-down">
        <button style="border:none;background-color:transparent;height:40px;float:left;padding-top:5px"><strong style="color:white">${event.word.toUpperCase()}:</strong> <span style="font-size:15px;"> ${event.meaning}</span></button>
        
       </div>
       `
      })
    }
}
        return (
            <div>
              
              <div className="container1">
              <div className="row">
                <form onSubmit={this.searchWord}>
                  <button type="submit" className="button" >Search</button>
                  <input type="text" value={this.state.word} onChange={this.handleWord} style={{float:"right"}}/>
                </form>
              </div>
                <div className="row"  >
                  <button className="button" onClick={writeThingBack} style={{float:"left"}} data-aos="zoom-in">PREVIOUS</button>
                  <button className="button" id="showHide" onClick={writeThingMean} data-aos="zoom-in">MEANING</button>
                  <button className="button" onClick={writeThingForward} style={{float:"right"}} data-aos="zoom-in">NEXT</button>
                </div>
              
                <div id="containword" data-aos="zoom-in">
                  <h2><span style={{color:"#282c34",fontSize:20 + "px"}}>{data.indexOf(data[wordIndex]) + 1}.</span>{data[wordIndex].word}</h2>
                </div>
                <div className ="row" style={{backgroundColor:"#282c34"}}>
                  <button className="button" onClick={showSimilar} data-aos="zoom-in">Show similar words</button>
                  <div>
                    <ul id="similarWords" ></ul>
                  </div>
                </div>
                <div id="containmean" data-aos="zoom-in">
                  
                </div>
                <div id="picContainer" data-aos="zoom-in">

                </div>
              
                <div className="row">
                  <button className="button" onClick={writeThingBackTen} style={{float:"left"}}> -5 </button>
                  <button className="button"  onClick={writeThingRandom} >RANDOM</button>
                  <button className="button" onClick={writeThingForwardTen} style={{float:"right"}} > +5 </button>
                </div>
                <div className="row">
                  <button className="button half" onClick={sortObjAsc} data-aos="fade-left">SORT ASCENDING</button>
                  <button className="button half" onClick={sortObjDesc} data-aos="fade-right">SORT DESCENDING</button>
                </div>
                <button id="button2" className="button2" onClick={displayAll}>SHOW ALL WORDS</button>
                <div  id="showAll" className="row" >
                </div>
              
              </div>
              
       
            </div>
        )
    }

  }

export default VocabCards
